<template>
	<div class="container">
		<div class="row">
			<div class="col-md-3">
				<Nav />
			</div>
			<div class="col-md-9">
				<div v-if="(!packageData || !versionData) || (!packageData.status && !versionData.status)">
					<div class="dashhead mb-3">
						<div class="dashhead-titles">
							<h6 class="dashhead-subtitle">{{packageData.name}} – Versions</h6>
							<h2 class="dashhead-title">Edit Version {{versionData.version}}</h2>
						</div>
					</div>
					
					<div class="row">
						<div class="col-6 text-left">
							<button class="btn btn-danger" :disabled="!$v.$anyDirty || isDoingWork" @click="cancel()">
								<span class="icon icon-squared-cross" />
								Cancel
							</button>
						</div>
						<div class="col-6 row p-0 justify-content-end text-right">
							<div class="col p-0 pr-3" v-if="isDoingWork">
								<div class="loading-indicator" />
							</div>
							<div class="col-auto p-0">
								<button class="btn btn-primary" @click="saveVersion()" :disabled="$v.$invalid || !$v.$anyDirty || isDoingWork">
									<span class="icon icon-save" />
									Save
								</button>
							</div>
						</div>
					</div>
				</div>
				
				<div v-if="packageData && packageData.status == 404">
					<vue-headful title="Package not found – FESTIVAL Pack'd" />
					<h4>Package not found</h4>
					<p>Could not retrieve info for package "{{ $route.params.packageId }}"</p>
				</div>
				
				<div v-if="versionData && versionData.status == 404">
					<vue-headful title="Version not found – FESTIVAL Pack'd" />
					<h4>Version not found</h4>
					<p>Could not retrieve info for version "{{ $route.params.versionId }}"</p>
				</div>
				
				<div v-if="(packageData && !packageData.status) && (versionData && !versionData.status)">
					<vue-headful title="Edit Version – FESTIVAL Pack'd" />
					<form name="addVersionForm" novalidate>
						<div class="row">
							<div class="col-md-6 form-group">
								<h4 class="my-4">Version Info</h4>
								
								<label>Version Number</label>
								<input type="text" class="form-control" placeholder="1.0" v-model="versionData.version" disabled>
								<p class="text-muted text-right m-b-0">{{50 - versionData.version.length}}</p>

								<label>Description</label>
								<textarea class="form-control" v-model="versionData.changeText" maxlength="4000" autocomplete="off" @input="$v.$touch()" style="height: 250px" :disabled="isDoingWork" />
								
								<div class="row">
									<div class="col-9">
										<p class="text-muted text-left m-b-0">Descriptions support HTML</p>
									</div>
									
									<div class="col-3">
										<p class="text-muted text-right m-b-0">{{4000 - versionData.changeText.length}}</p>
									</div>
								</div>
							</div>
							
							<div class="list-group col-md-6 m-b px-3">
								<h4 class="mb-4 mt-md-4">Options</h4>
								<div class="list-group-item d-flex justify-content-between align-items-center">
									<div class="col-auto p-0">
										<p class="m-0">Version is visible</p>
									</div>
									<div class="col-auto p-0">
										<SwitchCheckbox :class="{'disabled': isDoingWork}" checkboxName="visible" v-model="packageData.visible" @input="$v.$touch()" />
									</div>
								</div>
								
								<div class="row mt-4">
									<div class="col-6 mt-4 mt-md-0">
										<h4 class="mb-4">Package File</h4>
										<PackageFileSelector :class="{'disabled': isDoingWork}" v-model="selectedPackageFile" @input="$v.$touch()" />
									</div>
								</div>
							</div>
						</div>
					</form>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import Nav from '@/components/Nav.vue'
import PackageFileSelector from '@/components/PackageFileSelector'
import SwitchCheckbox from '@/components/SwitchCheckbox.vue'
import { PackageAPI } from '@/scripts/ApiUtil'
import { required } from 'vuelidate/lib/validators'

export default {
	name: "versionEdit",
	components: {
		Nav,
		PackageFileSelector,
		SwitchCheckbox
	},
	data() {
		return {
			packageData: null,
			versionData: {
				version: "",
				changeText: "",
				visible: true
			},
			selectedPackageFile: null,
			isDoingWork: false
		}
	},
	validations: {
		versionData: {
			version: { required },
			changeText: { required }
		},
	},
	async created() {
		this.isDoingWork = true;
		let _packageData = await PackageAPI.getPackage(this.$route.params.packageId);
		if (_packageData.status == 200) {
			this.packageData = _packageData.data;
		
			let _versionData = await PackageAPI.getPackageVersion(this.$route.params.packageId, this.$route.params.versionId);
			if (_versionData.status == 200) {
				this.versionData = Object.keys(_versionData.data)
					.filter(key => Object.keys(this.versionData).includes(key))
					.reduce((obj, key) => {
						obj[key] = _versionData.data[key];
						return obj
					}, {});
				
				this.isDoingWork = false;
			} else {
				this.isDoingWork = false;
				this.versionData = {
					status: _versionData.status
				}
			}
		} else {
			this.isDoingWork = false;
			this.packageData = {
				status: _packageData.status
			}
		}
	},
	mounted() {
		window.scrollTo(0,0);
	},
	methods: {
		cancel() {
			const self = this;
			bootbox.confirm({
				title: "Canel?",
				message: "Are you sure you want to cancel? All unsaved progress will be lost.",
				buttons: {
					confirm: {
						label: 'OK',
						className: 'btn-danger'
					},
					cancel: {
						label: 'Cancel',
						className: 'btn-secondary'
					}
				},
				callback: async (result) => {
					if (result) {
						self.$router.replace(`/packages/${this.$route.params.packageId}`);
					}
				}
			});
		},
		async saveVersion() {
			this.isDoingWork = true;
			
			let result = await PackageAPI.updatePackageVersion(this.$route.params.packageId, this.$route.params.versionId, this.versionData);
			if (result.status == 200) {
				if (this.selectedPackageFile) {
					let fileUploadResult = await PackageAPI.uploadPackageFile(this.$route.params.packageId, result.data.id, this.selectedPackageFile);
					if (fileUploadResult.status != 200) {
						this.isDoingWork = false;
						return bootbox.alert(`There was an error uploading the package file.<br>You need to manually upload the package file again through the Version manager.<br><br>${fileUploadResult.data.message}`);
					}
				}
				
				this.$v.$reset();
				this.isDoingWork = false;
			} else {
				this.isDoingWork = false;
				bootbox.alert(`There was an error updating the version.<br><br>${result.data.message}`);
			}
		},
		selectPackage(e) {
			this.$v.$touch();
			this.selectedPackageFile = e.target.files[0];
		}
	}
}
</script>